import { Component, OnInit , Inject, ViewChild} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import {Router} from "@angular/router";
import { Observable, of } from 'rxjs';
import { map, startWith, tap, withLatestFrom } from 'rxjs/operators';
import { User } from 'src/app/api/_models';
import { UserService } from 'src/app/api/_services';

@Component({
  selector: 'app-list-user',
  templateUrl: './list-user.component.html',
  styleUrls: ['./list-user.component.css']
})
export class ListUserComponent implements OnInit {

  users: User[];
  searchText: string = '';
  users$: Observable<User[]>;
  filteredUsers$: Observable<User[]>;
  formGroup: FormGroup;

  constructor(private router: Router, private userService: UserService, private formBuilder: FormBuilder) {
    this.formGroup = this.formBuilder.group({ filter: [''] });
   }

   private getUsers() {
    return of(this.users).pipe(tap());
  }

  ngOnInit() {

    if(!window.localStorage.getItem('currentUser')) {
      this.router.navigate(['login']);
      return;
    }

    if(window.location.pathname != "/login") {
      let elements = document.getElementsByClassName("navbar-nav");
      elements[0].setAttribute("style", "visibility: visible !important;")
    }

    this.userService.getAll()
      .subscribe( data => {
        this.users = data;
        this.formGroup = this.formBuilder.group({ filter: [''] });

      this.users$ = this.getUsers();

      this.filteredUsers$ = this.formGroup.get('filter').valueChanges.pipe(
        startWith(''),
        withLatestFrom(this.users$),
        map(([val, users]) =>
          !val ? users : users.filter((x) => x.username.toLowerCase().includes(val.toLowerCase()))
        )
      );
      });
  }

  deleteUser(user: User): void {
    this.userService.delete(user.id)
      .subscribe( data => {
        this.users = this.users.filter(u => u !== user);
      })
  };

  editUser(user: User): void {
    window.localStorage.removeItem("editUserId");
    window.localStorage.setItem("editUserId", user.id.toString());
    this.router.navigate(['edit-user']);
  };

  blockUser(user: User): void {
    this.userService.block(user.id)
      .subscribe( data => {
        this.users = this.users.filter(u => u !== user);
      })
      window.location.reload();
  };

  addUser(): void {
    this.router.navigate(['add-user']);
  };
}
