import { Component, OnInit , Inject} from '@angular/core';
import {Router} from "@angular/router";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {first} from "rxjs/operators";
import { User } from 'src/app/api/_models';
import { UserService } from 'src/app/api/_services';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {

  user: User;
  editForm: FormGroup;
  constructor(private formBuilder: FormBuilder,private router: Router, private userService: UserService) { }

  ngOnInit() {

    if(!window.localStorage.getItem('currentUser')) {
      this.router.navigate(['login']);
      return;
    }

    let userId = window.localStorage.getItem("editUserId");
    this.editForm = this.formBuilder.group({
      id: [],
      username: ['', Validators.required],
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: ['', Validators.required],
      phoneNumber: ['', Validators.required],
      city: ['', Validators.required],
      token: ['', Validators.required],
      password: ['', Validators.required],
      type: ['', Validators.required],
      isBlocked: ['', Validators.required],
    });
    this.userService.getUserById(+userId)
      .subscribe( data => {
        this.editForm.setValue(data);
      });
  }

  onSubmit() {
    this.userService.update(this.editForm.value)
      .pipe(first())
      .subscribe(
        data => {
          this.router.navigate(['users']);
        },
        error => {
          alert(error);
        });
  }

  editPackForUser(UserpackId: number) {
    window.localStorage.removeItem("userPackId");
    window.localStorage.setItem("userPackId", UserpackId.toString());
    this.router.navigate(['edit-userpack']);
  }

}
