import { ResetPasswordResolver } from './components/reset-password/reset-password.resolver';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { RouterModule } from '@angular/router';

import {MatFormFieldModule} from '@angular/material/form-field';
import {MatSelectModule} from '@angular/material/select';

import { AppComponent } from './app.component';
import { NavMenuComponent } from './components/nav-menu/nav-menu.component';
import { LoginComponent } from './components/Auth/login/login.component';
import { RegisterComponent } from './components/Auth/register/register.component';
import { AuthGuard, BasicAuthInterceptor, ErrorInterceptor } from './api/_helpers';
import { AddUserComponent } from './components/table/user/add-user/add-user.component';
import { ListUserComponent } from './components/table/user/list-user/list-user.component';
import { EditUserComponent } from './components/table/user/edit-user/edit-user.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatIconModule } from '@angular/material/icon';
import { ResetPasswordComponent } from './components/reset-password';


import { MatInputModule } from '@angular/material/input';

@NgModule({
  declarations: [
    AppComponent,
    NavMenuComponent,
    LoginComponent,
    RegisterComponent,

    //User
    AddUserComponent,
    EditUserComponent,
    ListUserComponent,

    ResetPasswordComponent,
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule,
    MatIconModule,
    MatSelectModule,
    MatFormFieldModule,
    FormsModule,
    BrowserModule,
    MatInputModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    RouterModule.forRoot([
      { path: '', component: ListUserComponent, canActivate: [AuthGuard] },
      { path: 'login', component: LoginComponent },
      { path: 'register', component: RegisterComponent },
      { path: 'users', component: ListUserComponent },

      { path: 'add-user', component: AddUserComponent },
      { path: 'edit-user', component: EditUserComponent },

      { path: 'reset-password', component: ResetPasswordComponent },

      { path: 'reset-password', component: ResetPasswordComponent },
      {
        path: 'reset-password/:token',
        component: ResetPasswordComponent,
        resolve: { token: ResetPasswordResolver }
      },

    ]),
    BrowserAnimationsModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: BasicAuthInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    ResetPasswordResolver ],
  bootstrap: [AppComponent]
})
export class AppModule { }
